"use client";

import Header from "./components/Header";
import {
  useReadContract,
  useWriteContract,
  useWaitForTransactionReceipt,
} from "wagmi";
import { parseAbi } from "viem";
import { useEffect } from "react";

const COUNTER_ADDRESS = process.env
  .NEXT_PUBLIC_COUNTER_ADDRESS as `0x${string}`;

const ABI = parseAbi([
  "function count() view returns (uint256)",
  "function increment()",
  "event Incremented(address indexed by, uint256 newValue)",
]);

export default function Home() {
  // read counter
  const { data: count, refetch } = useReadContract({
    address: COUNTER_ADDRESS,
    abi: ABI,
    functionName: "count",
  });

  // write contract
  const {
    writeContract,
    data: hash,
    isPending,
    error,
  } = useWriteContract();

  // wait for tx confirmation
  const { isLoading: isMining, isSuccess } =
    useWaitForTransactionReceipt({ hash });

  useEffect(() => {
    if (isSuccess) refetch();
  }, [isSuccess, refetch]);

  const onIncrement = () => {
    writeContract({
      address: COUNTER_ADDRESS,
      abi: ABI,
      functionName: "increment",
    });
  };

  return (
    <>
      <Header />
      <main className="mx-auto max-w-5xl p-6 space-y-6">
        <section className="rounded-2xl border border-zinc-800 p-6">
          <h2 className="text-lg font-medium mb-2">On-chain Counter</h2>
          <p className="text-sm text-zinc-400 mb-4">
            Network: Sepolia Testnet. Connect MetaMask and increment the counter.
          </p>

          <div className="text-5xl font-bold mb-6">
            {count?.toString() ?? "—"}
          </div>

          <button
            onClick={onIncrement}
            disabled={isPending || isMining}
            className="rounded-2xl px-5 py-2 bg-white/10 hover:bg-white/20 transition disabled:opacity-50"
          >
            {isPending ? "Sending…" : isMining ? "Confirming…" : "Increment"}
          </button>

          {hash && (
            <p className="mt-3 text-xs text-zinc-400 break-all">
              Tx: {hash}
            </p>
          )}
          {error && (
            <p className="mt-3 text-sm text-red-400">
              {error.message}
            </p>
          )}
        </section>
      </main>
    </>
  );
}
